.experience-container {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  & h2 {
    margin: 0;
    padding: 0 0 2rem 0;
    text-align: center;
  }
}

.list-experiences {
  padding: 0 3rem 3rem 3rem;
  @media screen and (max-width: 767px) {
    padding: 0 1rem 3rem 1rem;
  }
}

.experience {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;

  & .headers {
    display: flex;
    flex-direction: row;
    align-items: center;

    &--image {
      position: relative;
      width: 3rem;
      height: 3rem;
      margin-right: 1rem;
      @media screen and (max-width: 767px) {
        display: none;
      }

      & img {
        width: 100%;
        height: 100%;
        border-radius: 1rem;
      }
    }

    &--text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      h3 {
        margin: 0 0 1rem 0;
      }
      h4 {
        margin: 0;
      }
      h5 {
        margin: 0.5rem 0 0 0;
      }
    }
  }
  & .descriptions {
    & li {
      margin-bottom: 0.5rem;
    }
    @media screen and (max-width: 767px) {
      & ul {
        padding-left: 1rem;
      }
    }
  }
}
